import { useState, useEffect } from 'react';
import './App.css';
import Book from './Book';

function App() {

  const arr = [
    {name: "Henning", age: 32},
    {name: "Bobby", age: 31},
    {name: "Pim", age: 30}
  ]

  const [age, setAge] = useState(arr)
  const [data, setData] = useState([])
  const [name, setName] = useState("Baune")

  useEffect(()=>{
    fetch('https://jsonplaceholder.typicode.com/users')
    .then((res) => res.json())
    .then(data => setData(data))

  }, [])

  const handleDelete = (name) => {
    const newage = age.filter((a) => a.name !== name)

    setAge(newage);
  }


  return (
    <div>
      <h1>Looping through an array</h1>
      {age.map((index) => <p><button onClick={()=>{handleDelete(index.name)}}>Delete: {index.name}</button> at age {index.age}</p>)}
      {age.map((book, i) => <Book book={book} key={i}/>)}
      <button onClick={() => setName("Simon")}>Change Name</button>
      <h4>{name}</h4>
      <br></br>
      <h3>JSON data</h3>
      {data && data.map((user)=> <p>{user.name}</p>)}
    </div>
  );
}

export default App;
